<template>
  <div class="scheme" >
    <div class="List" v-for="item in data">
      <div class="banner" v-if='item.type=="BANNER"'>
        <router-link to='./' v-if="item.class_type=='index'"><img v-lazy="imgurl+item.img+'@!1200'" alt=""></router-link>
        <router-link to='./yearGoods' v-if="item.class_type=='yearGoods'"><img v-lazy="imgurl+item.img+'@!1200'" alt=""></router-link>
        <router-link :to='{path:"./detail",query:{id:item.url}}' v-else-if="item.class_type=='item'"><img v-lazy="imgurl+item.img+'@!1200'" alt=""></router-link>
        <router-link :to='{path:"./special",query:{sessionId:item.url}}' v-else-if="item.class_type=='session'"><img v-lazy="imgurl+item.img+'@!1200'" alt=""></router-link>
        <router-link :to='{path:"./active",query:{id:item.url}}' v-else-if="item.class_type=='huodong'"><img v-lazy="imgurl+item.img+'@!1200'" alt=""></router-link>
        <a v-bind:href='item.url'  v-else-if="item.class_type=='other'"><img v-lazy="imgurl+item.img+'@!1200'" alt=""></a>
      </div>
      <div class="itemList" v-else-if="item.type=='ITEM_LIST'" v-for='(itit,index) in item.items'>
        <div class="itemChild" v-if='itit.class_type=="index"'>
            <router-link  to="./" v-bind:key='index' >
                <img v-lazy="imgurl+itit.img+'@!800'" alt="">
            </router-link>
        </div>
        <div class="itemChild" v-if='itit.class_type=="yearGoods"'>
            <router-link  to='./yearGoods' v-bind:key='index' >
                <img v-lazy="imgurl+itit.img+'@!800'" alt="">
            </router-link>
        </div>
        <div class="itemChild" v-if='itit.class_type=="item"'>
            <router-link  :to="{path:'./detail',query:{id:itit.url}}" v-bind:key='index' >
                <img v-lazy="imgurl+itit.img+'@!800'" alt="">
            </router-link>
        </div>
        <div class="itemChild" v-if='itit.class_type=="session"'>
            <router-link  :to="{path:'./special',query:{sessionId:itit.url}}" v-bind:key='index' >
                <img v-lazy="imgurl+itit.img+'@!800'" alt="">
            </router-link>
        </div>
        <div class="itemChild" v-if='itit.class_type=="huodong"'>
            <router-link  :to="{path:'./active',query:{id:itit.url}}" v-bind:key='index' >
                <img v-lazy="imgurl+itit.img+'@!800'" alt="">
            </router-link>
        </div>
        <div class="itemChild" v-if='itit.class_type=="other"'>
            <a  v-bind:href="itit.url" v-bind:key='index' >
                <img v-lazy="imgurl+itit.img+'@!800'" alt="">
            </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'scheme',
  props:['blocks'],
  data () {
    return {
      msg: 'scheme',
      imgurl:'',
      data:''
    }
  },
  computed:{
      imgUrlPrefix () {
        return this.$store.state.imgUrlPrefix
      }
  },
  created(){
    this.imgurl = this.imgUrlPrefix ? this.imgUrlPrefix : localStorage.imgUrlPrefix;
    this.$watch('blocks',(newVal,oldval)=>{
      this.data = newVal;
      // console.log(this.data)
      this.data.forEach(item=>{
          if(item.type=='BANNER'){
              this.schemeType(item)

          }else if(item.type=='ITEM_LIST'){
              item.items.forEach(itit=>{
                this.schemeType(itit)
              })
          }
      })
      console.log(this.data)
    })

  },
  methods:{
    //判断Scheme类型
    schemeType(item){

      if(item.url.indexOf('pinkumall://default/mall_home')!=-1){
        //首页
        item.class_type = 'index'
      }else if(item.url.indexOf('pinkumall://default/huodong_home')!=-1){
        //活动首页 
        item.class_type = 'yearGoods'
      }else if(item.url.indexOf('pinkumall://default/item_detail')!=-1){
        //商品详情页
        item.url = item.url.split('?item_id=')[1];
        item.class_type = 'item'
      }else if(item.url.indexOf('pinkumall://default/session_detail')!=-1){
        //专场详情页
        item.url = item.url.split('?session_id=')[1];

        item.class_type = 'session'
      }else if(item.url.indexOf('pinkumall://default/huodong_detail')!=-1){
        //活动详情页
        item.url = item.url.split('?huodong_id=')[1];
        item.class_type = 'huodong'
      }else{
        //其他
        item.class_type = 'other'
      }
    }
  },
  mounted(){
    
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
@import '../../common/css/var.scss';
.scheme{
  .List{
    display: flex;
    flex-wrap:wrap;
    .banner{
      display: flex;
      width: 100%;
      a{
        flex:1;
        img{
          width: 100%;
          display: block;
        }
      }
    }
    .itemList{
      width: 50%;
        img{
          width: 100%;
          display: block;
        }
    }
  }
}
</style>



