import Vue from 'vue'
import Router from 'vue-router'
// import Home from '@/components/Home'
// import Cart from '@/components/Cart'
// import YearGoods from '@/components/YearGoods'
// import Special from '@/components/Special'
// import Detail from '@/components/Detail'
// import Login from '@/components/Login'
// import Submit_order from '@/components/Submit-order'
// import Paynow from '@/components/Paynow'
// import Payment_success from '@/components/Payment-success'
// import NewAddr from '@/components/NewAddr'
// import AddrList from '@/components/Addr-list'
// import Invoice from '@/components/Invoice'
// import Active from '@/components/Active'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: resolve => require(['@/components/Home'],resolve)
    },
    {
    	path: '/cart',
	    name: 'Cart',
	     component: resolve => require(['@/components/Cart'],resolve)
    },
    {
    	path: '/yearGoods',
	    name: 'YearGoods',
	    component: resolve => require(['@/components/YearGoods'],resolve)
    },
    {
    	path:'/special',
    	name: 'Special',
	    component: resolve => require(['@/components/Special'],resolve)
    },
    {
      path:'/detail',
      name: 'Detail',
      component: resolve => require(['@/components/Detail'],resolve)
    },
    {
      path:'/login',
      name: 'Login',
      component: resolve => require(['@/components/Login'],resolve)
    },
    {
      path:'/submit_order',
      name: 'submit_order',
      component: resolve => require(['@/components/Submit-order'],resolve)
    },
    {
      path:'/paynow',
      name: 'Paynow',
      component: resolve => require(['@/components/Paynow'],resolve)
    },
    {
      path:'/payment_success',
      name: 'payment_success',
      component: resolve => require(['@/components/Payment-success'],resolve)
    },
    {
      path:'/NewAddr',
      name: 'NewAddr',
      component: resolve => require(['@/components/NewAddr'],resolve)
    },
    {
      path:'/addrList',
      name: 'AddrList',
      component: resolve => require(['@/components/Addr-list'],resolve)
    },
    {
      path:'/invoice',
      name: 'Invoice',
      component: resolve => require(['@/components/Invoice'],resolve)
    },
    {
      path:'/active',
      name: 'Active',
      component: resolve => require(['@/components/Active'],resolve)
    }

    
  ]
})



