<template>
  <div id="footer">
      <router-link to='/'>
        <img v-if="footer.home" src="../../assets/Bottom_label_icon_home_click@3x.png" alt="">
        <img v-else src="../../assets/Bottom_label_icon_home_onclick@3x.png" alt="">
        
        <p>首页</p>
      </router-link>
      <router-link to='/yearGoods' class='nianhuo'>
        <img  v-if="footer.nianhuo" src="../../assets/Bottom_label_icon_year_click@3x.png" alt="">
        <img  v-else src="../../assets/Bottom_label_icon_year_onclick@3x.png" alt="">
        <p>买年货</p>
      </router-link>
      <h3 @click="toCart">
        <img v-if="footer.cart" src="../../assets/Bottom_label_icon_shopping_cart_click@3x.png" alt="">
        <img v-else src="../../assets/Bottom_label_icon_shopping_cart_onclick@3x.png" alt="">
        
        <p>购物车</p>
      </h3>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  name: 'Footer',
  data () {
    return {
      msg: 'suolun',
      footer:{}
    }
  },
  computed:{
      footerState () {
        return this.$store.state.footerState
      },
  },
  created(){
    this.footer = this.footerState ? this.footerState : JSON.parse(localStorage.footerState);
    // console.log(this.footer)
  },
  methods:{
    toCart(){ 
      this.$http({
        method:'get',
        url:this.$api+'/h5/users/check_login'
      }).then(res=>{
          this.$router.push({
            path:'./cart'
          })
      }).catch(error=>{
          
          if(error.request.status == 401){
            this.$store.commit('newRedirectUrl','/');
            window.localStorage.redirectUrl = '/';
            this.$router.push({
              path:'./login'
            })
          }else{
            let instance = Toast('网络好像有问题,请稍候再试试');
            setTimeout(() => {
              instance.close();
            }, 2000);
          }

      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  @import '../../common/css/var.scss';
  #footer{
    position:fixed;
    bottom: 0;
    width: 375px;
    height: 48px;
    left:0;
    background: white;
    display:flex;
    z-index: 99;
    width:100%;
    border-top: 1px solid #efefef;/*no*/
   transform: translate3d(0,0,0);
    a,h3{
      flex:1;
      display: flex;
      flex-direction:column;
      align-items:center;
      box-sizing:border-box;
      padding-top: 6px;
      position: relative;
      img{
        display: block;
        width: 24px;
        height: 24px;
        position: absolute;
        top: 5px;
        left: 50%;
        margin-left: -12px;
      }
      p{
        font-size:12px;
        transform:scale(0.9);
        color:$gray;
        line-height:20px;
        text-align:center;
        position: absolute;
        bottom: 0px;
        left: 50%;
        margin-left: -62.5px;
        width: 125px;
      }
    }
    .nianhuo{
      img{
        width: 34px;
        height: 34px;
        top: -10px;
        margin-left: -17px;
      }
    }
  }
</style>



