// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import store from './vuex/store'
import axios from 'axios'
import 'lib-flexible'
import Header from './components/common_components/header'
import Footer from './components/common_components/footer'
import GoodsList from './components/common_components/GoodsList'
import Scheme from './components/common_components/Scheme'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import Unit from './components/common_components/Unit'
import api from './api'
import VueLazyLoad from 'vue-lazyload'
import Mint from 'mint-ui';
import { Toast } from 'mint-ui';
import 'mint-ui/lib/style.css';
Vue.use(Mint);
Vue.component('Header',Header);
Vue.component('Footer',Footer);
Vue.component('GoodsList',GoodsList);
Vue.component('Scheme',Scheme);
Vue.component('Unit',Unit);
Vue.config.productionTip = false;
Vue.prototype.$http = axios;
Vue.prototype.$api = api.data
Vue.use(Vuex)
// swiper 插件
Vue.use(VueAwesomeSwiper) 
//图片懒加载
Vue.use(VueLazyLoad,{
    error:'./assets/homepage_placeholder1@3x.png',
    loading:'./assets/failed_to_load@3x.png'
})

Vue.filter('transMoney0', function (value) {
  if (!value) return '0'
  return parseFloat(value/10).toFixed(1)
})
Vue.filter('transMoney00', function (value) {
  if (!value) return '0'
  return parseFloat(value/100).toFixed(2)
})
Vue.filter('transMoney', function (value) {
  if (!value) return '0'
  return parseFloat(value/100)
})
Vue.filter('time',(value)=>{
   let aTime = new Date(value),
        nowTime = new Date(),
        countDownTime = aTime-nowTime,
        second = parseInt(countDownTime/1000)%60,
        min    = (parseInt(countDownTime/1000)-second)/60%60,
      hour   = ((parseInt(countDownTime/1000)-second)/60-min)/60%24,
      day    = (((parseInt(countDownTime/1000)-second)/60-min)/60-hour)/24;
    return (day>0?day+'天':'')+(hour<10?'0'+hour:hour)+':'+(min<10?'0'+min:min)+':'+(second<10?'0'+second:second)
  })


axios.get(api.data+'/h5/misc/config').then((res)=>{
  // console.log(res);
  store.commit('newImgUrlPrefix',res.data.value.imgUrlPrefix+'/');
  window.localStorage.imgUrlPrefix = res.data.value.imgUrlPrefix+'/';
  store.commit('newCsrfToken',res.data.value.csrfToken);
  window.sessionStorage.csrfToken = res.data.value.csrfToken;
   store.commit('newUserAgreementUrl',res.data.value.userAgreementUrl);
  window.localStorage.userAgreementUrl = res.data.value.userAgreementUrl;
  // _csrf_token_ 注释
  
  axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';
  axios.defaults.headers.post['_csrf_token_'] = sessionStorage.csrfToken;
  new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App }
  })
}).catch(error=>{
  let instance = Toast('请检查网络后,刷新重试');
    setTimeout(() => {
      instance.close();
    }, 5000);
    return ;
})





