<template>
  <div class="unit">
  </div>
</template>

<script>
import {getCookie} from '../../util/util';
export default {
  name: 'unit',
  data () {
    return {
      msg: 'unit'
    }
  },
  created(){
    this.getConfig();
  },
  methods:{
    getConfig(){
      this.$http({
        method:'get',
        url:this.$api+'/h5/misc/config',
      }).then(res=>{
        // console.log(res)
        this.$store.commit('newImgUrlPrefix',res.data.value.imgUrlPrefix+'/');
        window.localStorage.imgUrlPrefix = res.data.value.imgUrlPrefix+'/';
        this.$store.commit('newCsrfToken',res.data.value.csrfToken);
        window.localStorage.csrfToken = res.data.value.csrfToken;
      })
    }
  }
}
</script>





