import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  // 定义状态
  state: {
    author: 'suolun',
    footerState:{
      'home':false,
      'cart':false,
      'nianhuo':false
    },
    imgUrlPrefix:'',
    _csrf_token_:'',
    redirectUrl:'/',
    submit_order_data:[],
    order_detail_info:{},
    invoice_info:{},
    receiver_data:{},
    create_order_info:[],
    csrfToken:'',
    h5AssetUrlPrefix:'',
    userAgreementUrl:''
  },
  mutations:{
  	newAuthor(state,msg){
  		state.author = msg
  	},
    newImgUrlPrefix(state,msg){
      state.imgUrlPrefix = msg
    },
    new_csrf_token_(state,msg){
      state.new_csrf_token_ = msg
    },
    newRedirectUrl(state,msg){
      state.redirectUrl = msg
    },
    newSubmit_order_data(state,obj){
      state.submit_order_data = obj
    },
    newInvoice_info(state,obj){
      state.invoice_info = obj
    },
    newReceiver_data(state,obj){
      state.invoice_info = obj
    },
    newCreate_order_info(state,obj){
      state.orderId = obj
    },
    newFooterState(state,obj){
      state.footerState = obj
    },
    newCsrfToken(state,obj){
      state.csrfToken = obj
    },
    newH5AssetUrlPrefix(state,obj){
      state.h5AssetUrlPrefix = obj
    },
    newUserAgreementUrl(state,obj){
      state.h5AssetUrlPrefix = obj
    }
  }
})

export default store


